import React, { useState, useEffect } from 'react';
import { Row, Col, Card, Space, Typography, Button, Divider, Spin, Tabs, Flex } from 'antd';
import {
  FundViewOutlined, ApiOutlined,
  ArrowLeftOutlined,
  InfoCircleOutlined
} from '@ant-design/icons';
import type { AggregatedStats } from '../../components/Statistics/index';
import { mockAggregatedStats } from '../../../mock/monitorsystem.mock';
import StatCard from '../../components/Statistics/StatCard';
import DetailTable from '../../components/Statistics/DetailTable';
import QPSChart from '../../components/Statistics/QPSChart';
import MonitorRow from '../dashboard/analysis/components/MonitorRow';
import { useRequest } from '@umijs/max';
import { fakeChartData } from '../dashboard/analysis/service';
import MonitorCard, { CustomTab } from '../dashboard/analysis/components/MonitorCard';
import { getService, getServiceStats } from '@/components/WayPlus/request';

const { Title, Text } = Typography;

// 定义服务表格列
interface ServiceTableData {
  key: string;
  serviceName: string;
  totalRouters: number;
  totalRequests: number;
  totalQPS: number;
  avgQPS: number;
  totalErrors: number;
  errorRate: number;
  cacheHitRate: number;
  totalWSConnections: number;
  totalWSMessages: number;
  totalWSErrors: number;
}

const toCamelCase = (value: string) => {
  const camel = value.replace(/_([a-z])/g, (_, letter: string) => letter.toUpperCase());
  return camel
    .replace(/Qps\b/g, 'QPS')
    .replace(/Ws\b/g, 'WS');
};

const convertKeysToCamelCase = (input: unknown): any => {
  if (Array.isArray(input)) {
    return input.map(convertKeysToCamelCase);
  }
  if (!input || typeof input !== 'object') {
    return input;
  }
  return Object.fromEntries(
    Object.entries(input).map(([key, value]) => [
      toCamelCase(key),
      convertKeysToCamelCase(value),
    ]),
  );
};

const MonitorDashboard: React.FC = () => {
  const [loading, setLoading] = useState(true);
  const [selectedService, setSelectedService] = useState<AggregatedStats | null>(null);
  const [data, setData] = useState<AggregatedStats[]>([]);
  const { data: fakedata } = useRequest(fakeChartData);
  const [showRealData, setShowRealData] = useState(true);
  async function getStats() {
    const services = await getService();
    const results = await Promise.all(
      services.map((service: { Name: string }) => getServiceStats(service.Name)),
    );
    const aggregatedStats = results
      .filter((result) => result?.data)
      .map((result) => convertKeysToCamelCase(result.data))
      .flat();
    console.log(aggregatedStats)
    setData(aggregatedStats);
    return results;
  }
  useEffect(() => {
    if (showRealData) {
      getStats()
    } else {
      setData(mockAggregatedStats);
    }
    setLoading(false);
  }, [showRealData]);

  const handleServiceSelect = (service: AggregatedStats) => {
    setSelectedService(service);
  };

  const handleServiceDetail = (serviceName: string) => {
    const service = data.find(s => s.serviceName === serviceName);
    if (service) {
      setSelectedService(service);
    }
  };

  const handleBack = () => {
    setSelectedService(null);
  };

  // 计算所有服务的汇总统计
  const calculateOverallStats = (data: any[]) => {
    if (data.length === 0) return null;

    return {
      totalServices: data.length,
      totalRequests: data.reduce((sum, service) => sum + service.totalRequests, 0),
      maxQPS: Math.max(...data.map(service => service.totalQPS)),
      avgQPS: data.reduce((sum, service) => sum + service.avgQPS, 0) / data.length,
      totalErrors: data.reduce((sum, service) => sum + service.totalErrors, 0),
      cacheHitRate: data.reduce((sum, service) =>
        service.totalCacheHits + service.totalCacheMisses > 0 ?
          (service.totalCacheHits / (service.totalCacheHits + service.totalCacheMisses)) * 100 : 0, 0) / data.length,
      totalWSConnections: data.reduce((sum, service) => sum + service.totalWSConnections, 0),
      totalWSMessages: data.reduce((sum, service) => sum + service.totalWSMessages, 0),
      totalWSErrors: data.reduce((sum, service) => sum + service.totalWSErrors, 0),
    };
  };
  // 计算某个服务的汇总统计
  const calculateServiceStats = (data: any) => {
    if (!data) return null;
    const { totalRequests, totalCacheHits, totalCacheMisses, totalRouters, maxQPS, avgQPS, totalErrors, totalWSConnections, totalWSMessages, totalWSErrors } = data
    return {
      totalServices: totalRouters,
      totalRequests,
      maxQPS,
      avgQPS,
      totalErrors,
      cacheHitRate: totalCacheMisses + totalCacheHits > 0 ? (totalCacheHits / (totalCacheMisses + totalCacheHits)) * 100 : 0,
      totalWSConnections,
      totalWSMessages,
      totalWSErrors,
    };
  };


  // 获取所有服务的QPS历史数据用于图表展示
  const getAllServicesQPSHistory = () => {
    if (data.length === 0) return [];

    // 为每个服务生成当前QPS和最大QPS数据
    const chartData: any[] = [];

    data.forEach(service => {
      // 为服务生成QPS历史数据点
      if (service.routers.length > 0 && service.routers[0].qpsHistory) {
        service.routers[0].qpsHistory.forEach((qps, timeIndex) => {
          chartData.push({
            name: service.serviceName,
            qps: qps,
            index: timeIndex,
            type: '当前QPS'
          });
        });
      }

      // 添加服务的最大QPS数据点
      chartData.push({
        name: service.serviceName,
        qps: service.maxQPS,
        index: service.routers.length > 0 && service.routers[0].qpsHistory ?
          service.routers[0].qpsHistory.length : 0,
        type: '最大QPS'
      });
    });

    return chartData;
  };

  // 生成服务表格数据
  const getServiceTableData = (): ServiceTableData[] => {
    return data.map(service => ({
      key: service.serviceName,
      serviceName: service.serviceName,
      totalRouters: service.totalRouters,
      totalRequests: service.totalRequests,
      totalQPS: service.totalQPS,
      avgQPS: parseFloat(service.avgQPS?.toFixed(2)),
      maxQPS: parseFloat(service.maxQPS?.toFixed(2)),
      totalErrors: service.totalErrors,
      errorRate: service.totalRequests > 0 ?
        parseFloat(((service.totalErrors / service.totalRequests) * 100).toFixed(2)) : 0,
      cacheHitRate: service.totalCacheHits > 0 ?
        parseFloat(((service.totalCacheHits / (service.totalCacheHits + service.totalCacheMisses)) * 100).toFixed(2)) : 0,
      totalWSConnections: service.totalWSConnections,
      totalWSMessages: service.totalWSMessages,
      totalWSErrors: service.totalWSErrors,
    }));
  };

  // 如果选择了特定服务，显示详细信息
  if (selectedService) {
    const service = selectedService;
    console.log({ service })
    const overallStats = calculateServiceStats(service);
    const lineData = service.routers.flatMap((router) =>
      (router.qpsHistory ?? []).map((qps, index) => ({
        name: router.path,
        index,
        qps,
      })),
    );
    return (
      <div>
        <Space orientation="vertical" size="large" style={{ width: '100%' }}>
          {/* 汇总信息卡片 */}
          <Card>
            <Space align="center" style={{ width: '100%', justifyContent: 'space-between' }}>
              <Space>
                <Button icon={<ArrowLeftOutlined />} onClick={handleBack}>返回总览</Button>
                <Title level={4} style={{ margin: 0 }}>
                  {service.serviceName} 详细监控
                </Title>
              </Space>
              <Text type="secondary">
                更新时间: {new Date(service.collectedAt).toLocaleString()}
              </Text>
            </Space>
          </Card>

          {/* 汇总统计卡片 */}
          {overallStats && <MonitorRow loading={loading} overallStats={overallStats} visitData={fakedata?.visitData || []} />}

          <Card>
            <Tabs
              activeKey={'sad'}
              items={
                service.routers.map(s => {
                  return {
                    key: s.path,
                    label: (<CustomTab data={{ ...s, serviceName: s.path }} currentTabKey={""} />)
                  }
                })
              }
            />
          </Card>
          <MonitorCard loading={loading} salesData={lineData} rangePickerValue={null}
            isActive={() => ''}
            handleRangePickerChange={() => { }}
            selectDate={() => { }} />

          <Card>
            <Title level={5} style={{ marginTop: 0 }}>
              <ApiOutlined /> 路由详情
            </Title>
            <DetailTable
              data={service.routers.map(s => ({
                ...s,
                maxQPS: parseFloat(s.maxQPS?.toFixed(2)),
                avgQPS: parseFloat(s.avgQPS?.toFixed(2)),
              }))}
              loading={loading}
              type="router"
            />
          </Card>
        </Space>
      </div>
    );
  }

  // 显示所有服务的概览
  const overallStats = calculateOverallStats(data);
  const allServicesQPSHistory = getAllServicesQPSHistory();
  const serviceTableData = getServiceTableData();

  return (
    <Spin spinning={loading}>
      <Space orientation="vertical" size="large" style={{ width: '100%' }}>
        <Card>
          <Flex justify="space-between" align="center">
            <div>
              <Title level={4} style={{ margin: 0 }}>
                <FundViewOutlined /> 系统监控总览
              </Title>
              <Text type="secondary">实时监控系统各服务运行状态</Text>
            </div>
            <Button type='text' icon={<InfoCircleOutlined />} onClick={() => setShowRealData(!showRealData)}></Button>
          </Flex>
        </Card>

        {/* 汇总统计卡片 */}
        {overallStats && <MonitorRow loading={loading} overallStats={overallStats} visitData={fakedata?.visitData || []} />}

        {/* 所有服务的QPS趋势图 */}
        {/* <Card>
          <Title level={5} style={{ marginTop: 0 }}>
            <BarChartOutlined /> 所有服务QPS趋势
          </Title>
          <div style={{ height: 300 }}>
            <QPSChart data={allServicesQPSHistory} loading={loading} isOverview={true} />
          </div>
        </Card> */}
        <Card>
          <Tabs
            activeKey={'sad'}
            items={
              data.map(s => {
                return {
                  key: s.serviceName,
                  label: (<CustomTab data={s} currentTabKey={""} />
                  )
                }
              })
            }
          />
        </Card>
        <MonitorCard loading={loading} salesData={allServicesQPSHistory} rangePickerValue={null}
          isActive={() => ''}
          handleRangePickerChange={() => { }}
          selectDate={() => { }} />

        <Card>
          <Title level={5} style={{ marginTop: 0 }}>
            <ApiOutlined /> 服务详情
          </Title>
          <DetailTable
            data={serviceTableData}
            loading={loading}
            type="service"
            onServiceDetail={handleServiceDetail}
          />
        </Card>
      </Space>
    </Spin>
  );
};

export default MonitorDashboard;
