import React from 'react';
import { Table, Tag, Space, Button } from 'antd';
import type { ColumnsType } from 'antd/es/table';
import type { RouterStatsSnapshot } from './index';

interface RouterTableData extends RouterStatsSnapshot {
  key: string;
}


interface DetailTableProps {
  data: RouterStatsSnapshot[];
  loading?: boolean;
  type: 'router' | 'service';
  onServiceDetail?: (serviceName: string) => void;
}

const DetailTable: React.FC<DetailTableProps> = ({ data, loading, type, onServiceDetail }) => {
  // Router columns
  const routerColumns: ColumnsType<RouterTableData> = [
    {
      title: '服务名称',
      dataIndex: 'serviceName',
      key: 'serviceName',
    },
    {
      title: '路径',
      dataIndex: 'path',
      key: 'path',
    },
    {
      title: '方法',
      dataIndex: 'method',
      key: 'method',
      render: (method: string) => {
        const color = method === 'GET' ? 'green' : method === 'POST' ? 'blue' : method === 'PUT' ? 'orange' : 'red';
        return <Tag color={color}>{method}</Tag>;
      },
    },
    {
      title: '当前QPS',
      dataIndex: 'currentQPS',
      key: 'currentQPS',
      sorter: (a, b) => a.currentQPS - b.currentQPS,
    },
    {
      title: '平均QPS',
      dataIndex: 'avgQPS',
      key: 'avgQPS',
      sorter: (a, b) => a.avgQPS - b.avgQPS,
    },
    {
      title: '最大QPS',
      dataIndex: 'maxQPS',
      key: 'maxQPS',
      sorter: (a, b) => a.maxQPS - b.maxQPS,
    },
    {
      title: '总请求数',
      dataIndex: 'totalRequests',
      key: 'totalRequests',
      sorter: (a, b) => a.totalRequests - b.totalRequests,
    },
    {
      title: '错误数',
      dataIndex: 'totalErrors',
      key: 'totalErrors',
      sorter: (a, b) => a.totalErrors - b.totalErrors,
    },
    {
      title: '错误率',
      dataIndex: 'errorRate',
      key: 'errorRate',
      render: (errorRate: number) => `${errorRate.toFixed(2)}%`,
      sorter: (a, b) => a.errorRate - b.errorRate,
    },
    {
      title: '缓存命中率',
      dataIndex: 'cacheHitRate',
      key: 'cacheHitRate',
      render: (cacheHitRate: number) => `${cacheHitRate.toFixed(2)}%`,
      sorter: (a, b) => a.cacheHitRate - b.cacheHitRate,
    },
     {
      title: '操作',
      key: 'action',
      fixed: 'right',
      render: (_, record) => (
        <Button
          type="link"
          onClick={() => onServiceDetail && onServiceDetail(record.serviceName)}
        >
          详细信息
        </Button>
      ),
    },
  ];


  // Convert router data to table format
  const routerTableData: RouterTableData[] = Array.isArray(data) && type === 'router'
    ? data.map(router => ({
        ...router,
        key: `${router.serviceName}-${router.path}-${router.method}`
      }))
    : [];

  return (
    <Table
      columns={routerColumns}
      dataSource={data}
      loading={loading}
      rowKey="key"
      pagination={{ pageSize: 10 }}
      scroll={{ x: 1300 }}
    />
  );
};

export default DetailTable;
