import type { AggregatedStats } from '../src/components/Statistics/index';

// 生成随机数据的辅助函数
const randomInt = (min: number, max: number): number => Math.floor(Math.random() * (max - min + 1)) + min;
const randomFloat = (min: number, max: number, decimals: number = 2): number => parseFloat((Math.random() * (max - min) + min).toFixed(decimals));

// 生成QPS历史数据
const generateQPSHistory = (length: number = 10): number[] => {
  return Array.from({ length }, () => randomInt(50, 500));
};

// 生成路由统计快照
const generateRouterStats = (serviceName: string, index: number): any => {
  const methods = ['GET', 'POST', 'PUT', 'DELETE'];
  const paths = ['/api/users', '/api/products', '/api/orders', '/api/auth', '/api/search'];

  return {
    serviceName,
    path: paths[index % paths.length],
    method: methods[index % methods.length],
    currentQPS: randomInt(50, 300),
    maxQPS: randomInt(300, 800),
    avgQPS: randomFloat(100, 400),
    totalRequests: randomInt(1000, 10000),
    totalErrors: randomInt(0, 100),
    errorRate: randomFloat(0, 5),
    minResponseTime: `${randomInt(10, 50)}ms`,
    maxResponseTime: `${randomInt(200, 1000)}ms`,
    avgResponseTime: `${randomInt(50, 300)}ms`,
    cacheHits: randomInt(500, 5000),
    cacheMisses: randomInt(100, 1000),
    cacheHitRate: randomFloat(70, 95),
    cacheSize: randomInt(1000, 10000),
    qpsHistory: generateQPSHistory(),
    websocket: Math.random() > 0.5 ? {
      connections: randomInt(10, 100),
      messages: randomInt(1000, 10000),
      errors: randomInt(0, 20)
    } : undefined,
    uptime: `${randomInt(1, 30)}天${randomInt(0, 23)}小时${randomInt(0, 59)}分钟`,
    startTime: new Date(Date.now() - randomInt(86400000, 2592000000)).toISOString()
  };
};

// 生成聚合统计
const generateAggregatedStats = (serviceName: string): AggregatedStats => {
  const routers = Array.from({ length: randomInt(5, 15) }, (_, i) => generateRouterStats(serviceName, i));

  const totalRequests = routers.reduce((sum, router) => sum + router.totalRequests, 0);
  const totalErrors = routers.reduce((sum, router) => sum + router.totalErrors, 0);
  const totalQPS = routers.reduce((sum, router) => sum + router.currentQPS, 0);
  const avgQPS = totalQPS / routers.length;
  const maxQPS = Math.max(...routers.map(router => router.maxQPS));
  const totalCacheHits = routers.reduce((sum, router) => sum + router.cacheHits, 0);
  const totalCacheMisses = routers.reduce((sum, router) => sum + router.cacheMisses, 0);

  const hasWebSocket = routers.some(router => router.websocket);

  return {
    serviceName,
    totalRouters: routers.length,
    publicRouters: randomInt(2, routers.length),
    privateRouters: randomInt(1, routers.length - 2),
    manageRouters: randomInt(1, 3),
    totalRequests,
    totalErrors,
    totalQPS,
    avgQPS,
    maxQPS,
    totalCacheHits,
    totalCacheMisses,
    totalWSConnections: hasWebSocket ? randomInt(20, 200) : 0,
    totalWSMessages: hasWebSocket ? randomInt(1000, 20000) : 0,
    totalWSErrors: hasWebSocket ? randomInt(0, 50) : 0,
    routers,
    collectedAt: new Date().toISOString()
  };
};

// 生成多个服务的监控数据
export const mockAggregatedStats: AggregatedStats[] = [
  generateAggregatedStats('用户服务'),
  generateAggregatedStats('订单服务'),
  generateAggregatedStats('商品服务'),
  generateAggregatedStats('支付服务'),
  generateAggregatedStats('通知服务')
];
